      </header>
      <div
        style="padding-bottom: 80px"
        data-aos="fade-up"
        data-aos-duration="600"
        class="media-scroller snaps-inline"
      >
        <Scroller
          v-for="photo in photos.places"
          :key="photo.id"
          :photos="photo.path"
          :text="photo.text"
        ></Scroller>
      </div>
    </section>
  </div>
</template>

